@import "../_variables.scss";

.RouletteHeader {
    display: flex;
    align-items: flex-start;

    padding: get-vh(39px) get-vh(51px) 0;

    position: relative;
    z-index: 1;

    > .switch {
        display: flex;
        height: get-vh(38px);

        align-items: center;

        background: #121213;
        border-radius: get-vh(51px);

        padding: 0 get-vh(4px);
        margin-top: get-vh(55px);

        > .item {
            display: flex;
            height: get-vh(30px);

            align-items: center;

            color: rgba(255, 255, 255, 0.25);

            font-family: Montserrat-SemiBold, sans-serif;
            font-weight: 600;
            font-size: get-vh(12px);
            line-height: get-vh(15px);

            padding: 0 get-vh(24px);

            &.active {
                background: #FFDC5F;
                color: #252525;
                border-radius: get-vh(51px);
            }
        }
    }

    > .item {
        display: flex;
        align-items: center;

        &.current-balance {
            margin-left: get-vh(102px);
        }
        &.cases-opened-all-time,
        &.cases-best-drop-today {
            margin-left: get-vh(72px);
        }

        > svg {
            width: get-vh(59px);
            height: get-vh(59px);
        }

        > .wrap {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            margin-left: get-vh(18px);

            > span {
                color: #fff;

                &:first-child {
                    font-family: Montserrat-Medium, sans-serif;
                    font-weight: 500;
                    font-size: get-vh(14px);
                    line-height: get-vh(17px);
                }
                &:last-child {
                    font-family: Montserrat-Bold, sans-serif;
                    font-weight: bold;
                    font-size: get-vh(18px);
                    line-height: get-vh(22px);

                    margin-top: get-vh(3px);
                }
            }
        }
    }
}

.RouletteCases {
    display: grid;
    width: 100%;
    height: get-vh(718px);

    grid-template-columns: repeat(4, get-vh(322px));
    grid-auto-rows: get-vh(322px);
    grid-column-gap: get-vh(25px);
    grid-row-gap: get-vh(28px);

    overflow: hidden;
    overflow-y: auto;

    padding: get-vh(46px) get-vh(51px) 0;

    position: relative;
    z-index: 1;

    &::-webkit-scrollbar {
        width: 0;
    }

    > .CasesCase {
        display: flex;
        justify-content: center;

        background: rgba(0, 0, 0, 0.12);
        border-radius: get-vh(8px);

        padding-top: get-vh(258px);

        position: relative;

        > img {
            width: 90%;
            position: absolute;
            bottom: get-vh(113px);
        }

        @keyframes flyCase {
            0% {
                transform: translateY(0);
            }
            50% {
                transform: translateY(get-vh(-10px));
            }
            100% {
                transform: translateY(0);
            }
        }
        &:hover > img {
            animation: flyCase 1.5s ease-in-out infinite;
        }

        > .title {
            max-width: 100%;

            color: #fff;

            font-family: Montserrat-Bold, sans-serif;
            font-weight: bold;
            font-size: get-vh(16px);
            line-height: get-vh(20px);

            position: absolute;
            bottom: get-vh(75px);
        }

        > .buy {
            display: flex;
            height: get-vh(35px);

            padding-left: get-vh(38px);

            position: relative;

            > .button {
                display: flex;
                width: get-vh(88px);
                height: 100%;

                justify-content: center;
                align-items: center;

                background: #FFC76D;
                color: #252525;

                font-family: Montserrat-SemiBold, sans-serif;
                font-weight: 600;
                font-size: get-vh(14px);

                border-radius: get-vh(35px);

                position: absolute;
                top: 0;
                left: 0;
                transition: 250ms;

                &:hover {
                    background: #fab444;
                }
            }

            > .price {
                display: flex;
                min-width: get-vh(147px);
                height: 100%;


                align-items: center;
                justify-content: center;

                background: rgba(255, 255, 255, 0.05);
                color: #BDFF69;

                font-family: Montserrat-Bold, sans-serif;
                font-weight: bold;
                font-size: get-vh(14px);

                border-radius: get-vh(35px);

                padding-right: get-vh(10px);
                padding-left: get-vh(61px);
            }
        }
    }
}

.RouletteCase {
    display: flex;
    width: 100%;

    flex-direction: column;

    padding-top: get-vh(87px);

    position: relative;
    z-index: 1;

    > .Roulette {
        display: flex;
        width: 100%;
        height: get-vh(170px);

        justify-content: center;

        position: relative;

        &:before,
        &:after {
            content: "";
            display: block;
            width: get-vh(624px);
            height: 100%;

            position: absolute;
            top: 0;
        }
        &:before {
            background: linear-gradient(-90deg, rgba(0, 0, 0, 0.12) 0.05%, rgba(0, 0, 0, 0) 97.49%);
            left: 0;
        }
        &:after {
            background: linear-gradient(90.05deg, rgba(0, 0, 0, 0.12) 0.05%, rgba(0, 0, 0, 0) 97.49%);
            right: 0;
        }

        > .RouletteItems {
            width: 100%;
            height: 100%;

            overflow: hidden;

            position: relative;
            z-index: 1;

            > .items {
                display: flex;
                height: 100%;

                align-items: center;

                padding-left: get-vh(15px);

                position: absolute;
                top: 0;
                left: 0;

                &.type-0 {
                    transition: unset;
                    transform: translateX(0);
                }
                &.type-1 {
                    transition: transform 12s cubic-bezier(.09,.35,0,1);
                }
                &.type-2 {
                    transition: transform 12s cubic-bezier(.09,.35,0,1);
                    transform: translateX(-7460px);
                }
                &.type-3 {
                    transition: unset;
                    transform: translateX(-7460px);
                }

                > .RouletteCaseItem:not(:first-child) {
                    margin-left: get-vh(18px);
                }
            }
        }

        > .center {
            display: flex;
            width: get-vh(217px);
            height: get-vh(217px);

            justify-content: center;

            background: #1D1D1E;
            border-radius: get-vh(15px);

            position: absolute;
            top: get-vh(-24px);
            z-index: 2;

            > img {
                width: 83%;
                position: absolute;
                top: get-vh(37px);
            }

            > .title {
                color: #fff;

                font-family: Montserrat-Bold, sans-serif;
                font-weight: bold;
                font-size: get-vh(12px);

                text-align: center;

                position: absolute;
                bottom: get-vh(26px);
            }

            > .type {
                color: rgba(255, 255, 255, 0.55);

                font-family: Montserrat-Medium, sans-serif;
                font-weight: 500;
                font-size: get-vh(10px);

                position: absolute;
                bottom: get-vh(13.5px);
            }

            > svg.roulette-center-cursor {
                width: get-vh(286px);
                height: get-vh(285px);
                position: absolute;
                bottom: get-vh(-35px);
            }
        }

        > .actions {
            display: flex;
            flex-direction: column;
            align-items: center;

            position: absolute;
            top: calc(100% + #{get-vh(63px)});

            > .case-title {
                color: #FFFFFF;

                font-family: Montserrat-Bold, sans-serif;
                font-weight: bold;
                font-size: get-vh(16px);
                line-height: get-vh(20px);
            }

            > .open {
                display: flex;
                height: get-vh(35px);
    
                padding-left: get-vh(38px);
                margin-top: get-vh(11px);
    
                position: relative;
    
                > .button {
                    display: flex;
                    width: get-vh(88px);
                    height: 100%;
    
                    justify-content: center;
                    align-items: center;
    
                    background: #FFC76D;
                    color: #252525;
    
                    font-family: Montserrat-SemiBold, sans-serif;
                    font-weight: 600;
                    font-size: get-vh(14px);
    
                    border-radius: get-vh(35px);
    
                    position: absolute;
                    top: 0;
                    left: 0;
                    transition: 250ms;
    
                    &:hover {
                        background: #fab444;
                    }
                }
    
                > .price {
                    display: flex;
                    min-width: get-vh(147px);
                    height: 100%;
    
                    align-items: center;
                    justify-content: center;
    
                    background: rgba(255, 255, 255, 0.05);
                    color: #BDFF69;
    
                    font-family: Montserrat-Bold, sans-serif;
                    font-weight: bold;
                    font-size: get-vh(14px);
    
                    border-radius: get-vh(35px);
    
                    padding-right: get-vh(10px);
                    padding-left: get-vh(61px);
                }
            }
        }
    }

    > .RouletteItems {
        display: flex;
        width: 100%;

        flex-direction: column;

        padding-top: get-vh(149px);
        
        > .title {
            display: flex;
            width: 100%;

            color: #fff;

            font-family: Montserrat-Bold, sans-serif;
            font-weight: bold;
            font-size: get-vh(16px);
            line-height: get-vh(20px);

            padding-left: get-vh(79px);
        }

        > .items {
            display: grid;
            width: 100%;
            height: get-vh(285px);

            grid-template-columns: repeat(9, get-vh(135px));
            grid-auto-rows: get-vh(135px);
            grid-gap: get-vh(15px);

            overflow: hidden;
            overflow-y: auto;

            padding: 0 get-vh(65px);
            margin-top: get-vh(37px);

            &::-webkit-scrollbar {
                width: 0;
            }
        }
    }
}

.RouletteCaseItem {
    display: flex;
    width: get-vh(135px);
    height: get-vh(135px);

    flex-shrink: 0;
    flex-direction: column;
    align-items: center;

    justify-content: center;

    background: rgba(0, 0, 0, 0.12);
    border-radius: get-vh(15px);

    position: relative;

    > img {
        width: 83%;
        position: absolute;
        bottom: get-vh(39px);
    }

    > .title {
        color: #fff;

        font-family: Montserrat-Bold, sans-serif;
        font-weight: bold;
        font-size: get-vh(12px);

        text-align: center;

        position: absolute;
        bottom: get-vh(25px);
    }

    > .type {
        color: rgba(255, 255, 255, 0.55);

        font-family: Montserrat-Medium, sans-serif;
        font-weight: 500;
        font-size: get-vh(10px);

        position: absolute;
        top: get-vh(110px);
    }
}

.RouletteContainers {
    display: grid;
    width: get-vh(1360px);
    height: get-vh(457px);

    grid-template-columns: repeat(4, get-vh(304px));
    grid-auto-rows: get-vh(457px);
    grid-gap: get-vh(48px);

    overflow: hidden;
    overflow-y: auto;

    margin-top: get-vh(28px);
    margin-left: get-vh(57px);

    position: relative;
    
    &::-webkit-scrollbar {
        width: 0;
    }
}

