@import "./variables.scss";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    > .wrap {
        display: flex;
        width: get-vh(1465px);
        height: get-vh(904px);

        flex-direction: column;

        position: relative;

        &.containers {
            height: get-vh(667px);
        }

        > .title {
            top: get-vh(34px);
            left: get-vh(51px);
            font-family: Montserrat;
            font-size:  get-vh(36px);
            font-style: normal;
            font-weight: 800;
            line-height: get-vh(44px);
            letter-spacing: 0em;
            text-align: left;
            background: linear-gradient(90deg, #FF27F6 0%, #FF2E2E 98.79%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            position: absolute;
        }

        > .RouletteBG {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }

        > svg.RouletteClose {
            width: get-vh(38px);
            height: get-vh(38px);
            position: absolute;
            top: get-vh(21px);
            right: get-vh(21px);
            z-index: 2;

            &:hover path {
                stroke: white;
                stroke-opacity: 1;
            }
        }
    }
}

.fullscreen {
    position: absolute;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 0;
    > .reward {
        position: absolute;
        width: get-vh(550px);
        height: get-vh(365px);
        left: get-vh(685px);
        top: get-vh(357px);
        > .your-reward {
            font-family: Montserrat;
            font-size: get-vh(36px);
            font-style: normal;
            font-weight: 800;
            line-height: get-vh(44px);
            letter-spacing: 0em;
            text-align: center;
            color: #FFFFFF;
        }
        > .reward-name {
            font-family: Montserrat;
            font-size: get-vh(25px);
            font-style: normal;
            font-weight: 800;
            line-height: get-vh(30px);
            letter-spacing: 0em;
            text-align: center;
            background: linear-gradient(90deg, #FF27F6 0%, #FF2E2E 98.79%);
            background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        > .icon-wrapper {
            width: get-vh(128px);
            height: get-vh(128px);
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: get-vh(55px);
            margin-left: get-vh(211px);
            > .reward-icon {
                width: get-vh(128px);
            }
        }
        > .wrapper {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            > .claim-btn {
                color: #FFFFFF;
                font-family: Montserrat;
                font-size: get-vh(14px);
                font-style: normal;
                font-weight: 600;
                line-height: get-vh(17px);
                letter-spacing: 0em;
                text-align: left;
                width: fit-content;
                background: linear-gradient(90deg, #FF27F6 0%, #FF2A93 38.36%, #FF2C57 68.47%, #FF2E2E 98.79%);
                border-radius: get-vh(35px);
                padding: get-vh(11px) get-vh(19px) get-vh(11px) get-vh(19px);
                margin-top: get-vh(55px);
            }
        }
    }
}